import React, { useState } from 'react';
import Header from './Header';
import { useHistory } from 'react-router-dom';

const Home = () => {
  const [userText, setuserText] = useState('');
  const history = useHistory();

  const massaghandler = () => {
    if (userText.length <= 0) {
      alert('한글자이상입력해주세요');
    } else if (userText === 'qja8658@nate.com') {
      alert('30일 무료에 당첨되셨습니다 영화 리스트로이동합니다');
      history.push('/movie/list');
    } else {
      alert('당첨되지않았습니다 다음번에이용해주세용');
    }
  };
  return (
    <div>
      <div className="container1">
        <Header />
        <div className="main-card">
          <div className="mant-card">
            <h1>영화, TV 프 로 그 램을 무제한으로.</h1>
            <div className="sub-title">
              <h2>
                다양한 디바이스에서 시청하세요. 언제든 해지하실 수 있습니다.
              </h2>
              <form className="main-email">
                <h3>
                  시청할 준비가 되셨나요? 멤버십을 등록하거나 재시작하려면
                  이메일 주소를 입력하세요.
                </h3>
                <div className="input-box">
                  <input
                    type="text"
                    placeholder="이메일을 입력하세요"
                    value={userText}
                    onChange={(e) => {
                      setuserText(e.target.value);
                    }}
                    onKeyDown={(e) => {
                      if (e.keyCode === 13) {
                        massaghandler();
                      }
                    }}
                  />
                  <button onClick={massaghandler}>30일무료이용</button>
                </div>
                <h3> 당첨된회원만 이 프로모션을 이용하실 수 있습니다.</h3>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div className="card">
        <div className="contants-box">
          <div className="main-txt">
            <h1>이젠 TV로즐기세요</h1>
            <h2>
              스마트 TV, PlayStation, Xbox, Chromecast, Apple TV, 블루레이
              플레이어 등 다양한 디바이스에서 시청하세요.
            </h2>
          </div>
          <div className="img-box">
            <img src="https://assets.nflxext.com/ffe/siteui/acquisition/ourStory/fuji/desktop/tv.png" />
            <video
              src="https://assets.nflxext.com/ffe/siteui/acquisition/ourStory/fuji/desktop/video-tv-0819.m4v"
              autoPlay
              loop="10"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Home;
