import React, { useEffect, useState } from 'react';
import { useHistory, useParams, Link } from 'react-router-dom';
import SubHeader from '../movielist/SubHeader';
import axios from 'axios';
const Detail = () => {
  const [movieData, setMovieData] = useState({});
  const [error, setError] = useState(null);
  const [onView, setOnView] = useState(false);
  const { id } = useParams();

  const viewHandler = () => {
    setOnView(!onView);
    console.log(onView);
  };
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        // 요청이 시작 할 때에는 error 와 users 를 초기화하고

        const response = await axios.get(
          'https://yts-proxy.now.sh/list_movies.json'
        );
        const copyData = { ...response.data.data.movies[id] };
        setMovieData(copyData); // 데이터는 response.data 안에 들어있습니다.
        // console.log(copyData);
        console.log(copyData);
      } catch (e) {
        setError(e);
      }
    };

    fetchUsers();
  }, []);

  return (
    <>
      <Link to="./">
        <SubHeader />
      </Link>
      <div className="detail-title">
        <div>
          <h1>상세페이지</h1>
        </div>
      </div>
      <div className="background">
        <img className="back" src={movieData && movieData.background_image} />
        <img
          className="imgbox"
          src={movieData && movieData.large_cover_image}
        />
        <div className="detail-main">
          <div className="info-box">
            <div style={{}}>
              <h1 style={{ marginBottom: '5%' }}>
                영화제목 -{movieData.title}
              </h1>
            </div>
            <h2 style={{ paddingBottom: '5%' }}>장르-{movieData.genres}</h2>
            <div>
              <h2 style={{ paddingBottom: '5%' }}>
                {' '}
                영화 평점 -{movieData.rating}
              </h2>
            </div>
            <div style={{ paddingBottom: '5%', paddingBottom: '10%' }}>
              <div style={{ paddingBottom: '5%' }} onClick={viewHandler}>
                {onView === true ? (
                  <> 줄거리 닫기  👆</>
                ) : (
                  <> 줄거리 보기  👇</>
                )}
              </div>

              {onView === true ? (
                <div className="contns-text-box">{movieData.synopsis}</div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Detail;
