import React from 'react';
import './App.css';
import './style.css';
import Home from './Home';
import { Route, Switch, useParams } from 'react-router-dom';
import MovieList from './movielist/MovieList';
import Detail from './detail/Detail';

function App() {
  return (
    <div className="App">
      <div className="container">
        <Switch>
          <Route exact component={Home} path="/" />
          <Route exact component={MovieList} path="/movie/list" />
          <Route exact component={Detail} path="/movie/detali/:id" />
          <Route ex path="/:id">
            <div style={{ color: 'white' }}>올바르지 않은 주소입니다</div>
          </Route>
        </Switch>
      </div>
    </div>
  );
}

export default App;
